<template>
    <div>
        
    </div>
</template>

<script>
import { Promise } from 'q';
import { setTimeout } from 'timers';
export default {
    name: 'geolocation',
    data(){
        return {
            location: {
                longitud: undefined,
                latitud: undefined,
            }
        }
    },
    props: {
        callback: Function
    },
    mounted: function() {
         let _this=this;
         new Promise((resolve, reject)=>{
    if(navigator.geolocation){
       navigator.geolocation.getCurrentPosition((position)=>{
            this.location.latitud =position.coords.latitude;
            this.location.longitud =position.coords.longitude;
            this.callback(this.location);
           resolve();
       },(error)=>{
           fetch("http://www.geoplugin.net/json.gp")
           .then(function(response) {
             return response.json();
            })
           .then(resp=>{
            _this.location.latitud =resp.geoplugin_latitude;
            _this.location.longitud =resp.geoplugin_longitude;
            _this.callback(_this.location);
           }).catch(error=>reject(error))           
       },{
            enableHighAccuracy: true,
            timeout: 1500,
            maximumAge: 0
       })
    }}),

    setTimeout(()=>{
         fetch("http://www.geoplugin.net/json.gp")
           .then(function(response) {
             return response.json();
            })
           .then(resp=>{
               _this.location.latitud =resp.geoplugin_latitude;
            _this.location.longitud =resp.geoplugin_longitude;
            _this.callback(_this.location);
           }).catch(error=>reject(error))
    },2000)
  }
}
</script>